 @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

body, html, .osjs-root {
    font-family: "Montserrat"!important;
    bottom: 0;
    color: #fff;
    background-color: #000000;
    opacity: 1;
    background-image: linear-gradient(30deg, #ff0016 12%, transparent 12.5%, transparent 87%, #ff0016 87.5%, #ff0016), linear-gradient(150deg, #ff0016 12%, transparent 12.5%, transparent 87%, #ff0016 87.5%, #ff0016), linear-gradient(30deg, #ff0016 12%, transparent 12.5%, transparent 87%, #ff0016 87.5%, #ff0016), linear-gradient(150deg, #ff0016 12%, transparent 12.5%, transparent 87%, #ff0016 87.5%, #ff0016), linear-gradient(60deg, #ff001677 25%, transparent 25.5%, transparent 75%, #ff001677 75%, #ff001677), linear-gradient(60deg, #ff001677 25%, transparent 25.5%, transparent 75%, #ff001677 75%, #ff001677);
    background-size: 20px 35px;
    background-position: 0 0, 0 0, 10px 18px, 10px 18px, 0 0, 10px 18px;
}
 
 
 .osjs-panel-item[data-name=menu]>div>img {
     content: url(server.svg);
     background-repeat: no-repeat;
     background-position: center;
     background-size: contain;
     filter: invert(100%) !important;
     display: flex;
     justify-content: center;
     align-items: center;
     width: auto;
     height: 16px;
     margin: auto;
 }
 
 .osjs-panel-item[data-name=menu]:hover {
     filter: invert(0%);
 }
 
 .osjs-panel:hover .osjs-panel-item[data-name=menu]>div>img {
     filter: invert(0%);
 }
 
 .osjs-panel-item[data-name=menu]>div>span{
     display: none;
 }
 .osjs-panel-item[data-name=tray]>div>img {
     content: url('https://fonts.gstatic.com/s/i/materialiconsoutlined/search/v1/24px.svg');
     background-repeat: no-repeat;
     background-position: center;
     background-size: contain;
     filter: invert(100%);
 }
 
 .osjs-panel-item[data-name=tray]>div>img:hover {
     filter: invert(0%);
 }
 
 ::-webkit-scrollbar {
     width: 1em;
     height: 1em;
 }
 
 ::-webkit-scrollbar-button {
     background: linear-gradient(180deg, #1a1a1a 0, #000000);
     color: #d9d9d9;
     width: 1em;
     height: 1em;
     display: none;
 }
 
 ::-webkit-scrollbar-track {
     border: 1px solid #000000;
     background: #1a1a1a;
 }
 
 ::-webkit-scrollbar-track-piece {
     background: #1a1a1a;
 }
 
 ::-webkit-scrollbar-thumb {
     border: 1px solid #000000;
     background: linear-gradient(180deg, #1a1a1a 0, #000000);
 }
 
 ::-webkit-resizer,
 ::-webkit-scrollbar-corner {
     background: #1a1a1a;
 }
 
 .osjs-gui-button>button {
     border: 1px solid #000000;
     background: #1a1a1a;
     color: #fff;
 }
 
 .osjs-gui-button>button:focus {
     outline: 1px solid rgba(0, 0, 200, .2);
 }
 
 .osjs-gui-button>button:disabled {
     opacity: .8;
 }
 
 .osjs-gui-button>button:active,
 .osjs-gui-button>button[active=true] {
     border: 1px solid #333;
     background: #1a1a1a;
     outline: 0 none;
 }
 
 .osjs-gui-button>button:disabled {
     color: #fff;
 }
 
 .osjs-gui-field input,
 .osjs-gui-field select,
 .osjs-gui-field textarea,
 .osjs-gui-input input,
 .osjs-gui-input select,
 .osjs-gui-input textarea {
     border: 1px solid #000000;
     background: #333;
     color: #fff;
     font-size: inherit;
     font-family: Roboto, sans-serif;
 }
 
 .osjs-gui-field input:focus,
 .osjs-gui-field select:focus,
 .osjs-gui-field textarea:focus,
 .osjs-gui-input input:focus,
 .osjs-gui-input select:focus,
