 .osjs-gui-field input:disabled,
 .osjs-gui-field select:disabled,
 .osjs-gui-field textarea:disabled,
 .osjs-gui-input input:disabled,
 .osjs-gui-input select:disabled,
 .osjs-gui-input textarea:disabled {
     opacity: .8;
     color: #fff;
 }
 
 .osjs-gui-tabs-header>div {
     border: 1px solid #000000;
     background: #1a1a1a;
 }
 
 .osjs-gui-tabs-header>div.osjs__active {
     border-bottom-color: #1a1a1a;
     background-color: transparent;
 }
 
 .osjs-gui-tabs-panes {
     margin-top: -1px;
 }
 
 .osjs-gui-tabs-panes>div {
     border: 1px solid #000000;
     background: #1a1a1a;
 }
 
 .osjs-gui-tabs-panes>div.osjs__active {
     background-color: transparent;
 }
 
 .osjs-gui-toggle-field>label>span.osjs-toggle-input:before {
     border: 1px solid #000000;
     background: #1a1a1a;
     color: #fff;
 }
 
 .osjs-gui-toggle-field>label>span.osjs-toggle-input:before:focus {
     outline: 1px solid rgba(0, 0, 200, .2);
 }
 
 .osjs-gui-toggle-field>label>span.osjs-toggle-input:before:disabled {
     opacity: .8;
 }
 
 .osjs-gui-toggle-field>label>span.osjs-toggle-input:before:active,
 .osjs-gui-toggle-field>label>span.osjs-toggle-input:before[active=true] {
     border: 1px solid #333;
     background: #1a1a1a;
     outline: 0 none;
 }
 
 .osjs-gui-toggle-field>label>span.osjs-toggle-input:before:disabled {
     color: #fff;
 }
 
 .osjs-gui-toggle-field>label>span.osjs-toggle-input:after {
