 }
 
 .osjs-gui-toggle-field>label>input[type=radio]+span.osjs-toggle-input:before {
     border-radius: 100%;
 }
 
 .osjs-gui-range-field>input[type=range] {
     background: transparent;
     border: 0;
     margin: 0;
     padding: 0;
     overflow: visible;
     height: 2em;
     -webkit-appearance: none;
     width: 100%;
 }
 
 .osjs-gui-range-field>input[type=range]::-webkit-slider-runnable-track {
     width: 100%;
     height: .5em;
     background: #1a1a1a;
     border-radius: 0;
     border: 0;
     cursor: pointer;
 }
 
 .osjs-gui-range-field>input[type=range]::-webkit-slider-thumb {
     border: 1px solid #000000;
     height: 1em;
     width: 1em;
     border-radius: 0;
     background: #1a1a1a;
     margin-top: -.25em;
     cursor: pointer;
     -webkit-appearance: none;
 }
 
 .osjs-gui-range-field>input[type=range]::-moz-range-track {
     width: 100%;
     height: .5em;
     background: #1a1a1a;
     border-radius: 0;
     border: 0;
     cursor: pointer;
 }
 
 .osjs-gui-range-field>input[type=range]::-moz-range-thumb {
     border: 1px solid #000000;
     height: 1em;
     width: 1em;
     border-radius: 0;
     background: #1a1a1a;
     margin-top: -.25em;
     cursor: pointer;
 }
 
 .osjs-gui-range-field>input[type=range]::-ms-track {
     width: 100%;
     height: .5em;
     background: #1a1a1a;
     border-radius: 0;
     cursor: pointer;
     background: transparent;
     border: 0 transparent;
     color: transparent;
 }
 
 .osjs-gui-range-field>input[type=range]::-ms-fill-lower,
 .osjs-gui-range-field>input[type=range]::-ms-fill-upper {
     background: #1a1a1a;
     border: 1px solid #000000;
     border-radius: 0;
 }
 
 .osjs-gui-range-field>input[type=range]::-ms-thumb {
     border: 1px solid #000000;
     height: 1em;
     width: 1em;
     border-radius: 0;
     background: #1a1a1a;
     margin-top: -.25em;
     cursor: pointer;
 }
 
 .osjs-gui-icon-view-wrapper {
     border: 1px solid #000000;
     background: #333;
     color: #fff;
 }
 
 .osjs-gui-icon-view-wrapper:focus {
     outline: 1px solid rgba(0, 0, 200, .2);
 }
 
 .osjs-gui-icon-view-wrapper:disabled {
     opacity: .8;
     color: #fff;
 }
 
 .osjs-gui-icon-view-wrapper .osjs__active .osjs__image,
 .osjs-gui-icon-view-wrapper .osjs__active .osjs__label {
     background: rgba(45, 115, 210, .2);
     color: #fff;
 }
 
 .osjs-gui-menu ul {
     box-shadow: 0 0 1em rgba(0, 0, 0, .5);
     background: rgba(0, 0, 0, .9);
     color: #fff;
 }
 
 .osjs-gui-menu li:hover>div>span {
     background-color: #fff;
     color: #000;
 }
 
 .osjs-gui-menu .osjs__disabled {
     opacity: .5;
 }
 
 .osjs-gui-menubar>div {
     cursor: pointer;
 }
 
 .osjs-gui-menubar>div:hover {
     background-color: rgba(45, 115, 210, .2);
     color: #fff;
 }
 
 .osjs-notification {
     box-shadow: 0 0 1em rgba(0, 0, 0, .5);
     background: rgba(0, 0, 0, .9);
     color: #fff;
 }
 
 .osjs-notification .osjs-notification-title {
     font-weight: 700;
 }
 
 .osjs-notification .osjs-notification-message {
     margin-top: 1em;
 }
 
 .osjs-gui-progressbar-wrapper {
     border: 1px solid #000000;
     background: #1a1a1a;
     color: #fff;
 }
 
 .osjs-gui-progressbar-value {
     background: rgba(0, 0, 200, .5);
 }
 
 .osjs-gui-list-view .osjs__active {
     background: rgba(45, 115, 210, .2);
     color: #fff;
 }
 
 .osjs-gui-list-view .rows:not([data-zebra=false])>div:nth-child(odd):not(.osjs__active) {
     background-color: #5a5a5a;
 }
 
 .osjs-gui-list-view-wrapper {
     border: 1px solid #000000;
     background: #333;
     color: #fff;
 }
 
 .osjs-gui-list-view-wrapper:focus {
     outline: 1px solid rgba(0, 0, 200, .2);
 }
 
 .osjs-gui-list-view-wrapper:disabled {
     opacity: .8;
     color: #fff;
 }
 
 .osjs-gui-list-view-header {
     background: #1a1a1a;
     border: 0;
 }
 
 .osjs-gui-box-styled {
     border: 1px solid #000000;
     background: #333;
     color: #fff;
 }
 
 .osjs-gui-box-styled:focus {
     outline: 1px solid rgba(0, 0, 200, .2);
 }
 
 .osjs-gui-box-styled:disabled {
     opacity: .8;
     color: #fff;
 }
 
 .osjs-gui-expander-header {
     background: #1a1a1a;
     border: 0;
 }
 
 .osjs-window[data-minimized=true] {
     opacity: 0;
     pointer-events: none;
     display: block;
 }
 
 .osjs-window[data-minimized=false] {
     display: block;
 }
 
 .osjs-panel-item[data-name=windows]>div,
 .osjs-window:not([data-moving=true]):not([data-resizing=true]) {
     transition: all .1s ease-in-out;
 }
 
 .osjs-window-button>div:after,
 .osjs-window-button>div:before {
     content: "";
     position: absolute;
     top: 50%;
     left: 50%;
     display: block;
     width: 1em;
     height: 1em;
     border-radius: 100%;
     box-sizing: border-box;
     transform: translate(-50%, -50%);
 }
 
 .osjs-window-button>div:before {
     background: #bababa;
     background: linear-gradient(180deg, #bababa 0, #d9d9d9);
     z-index: 1;
 }
 
 .osjs-window-button>div:after {
     border: 1px solid hsla(0, 0%, 40%, .5);
     z-index: 2;
 }
 
 .osjs-window {
     border: 1px solid #000000;
     background: #1a1a1a;
     border-radius: 4px;
     box-shadow: 0 0 1em rgba(0, 0, 0, .5);
 }
 
 .osjs-window[data-focused=false] {
     opacity: .5;
 }
 
 .osjs-window[data-loading=true] {
     pointer-events: none;
 }
 
 .osjs-window[data-loading=true]:after {
     background: rgba(0, 0, 0, .5);
 }
 
 .osjs-window[data-focused=true] .osjs-window-button[data-action=close]>div:before {
     background: #e56c5c;
     background: linear-gradient(180deg, #e56c5c 0, #f09c8d);
 }
 

 /* Add this in your existing CSS */
.osjs-panel-item--clickable:hover img {
    filter: brightness(0); /* This will turn the image darker */
  }
  
  .osjs-panel-item--clickable:hover {
    background-color: #fff; /* Optional: If you also want to change the background on hover */
    color: rgba(0, 0, 0, 0.9); /* Change the text color on hover */
    box-shadow: 0 0 1em hsla(0, 0%, 100%, 0.5); /* Optional: Add a slight shadow effect on hover */
  }

  
 .osjs-window[data-focused=true] .osjs-window-button[data-action=close]:active>div:before,
 .osjs-window[data-focused=true] .osjs-window-button[data-action=close]:hover>div:before {
     background: #f09c8d;
     background: linear-gradient(180deg, #f09c8d 0, #e56c5c);
 }
 
 .osjs-window[data-focused=true] .osjs-window-button[data-action=maximize]>div:before {
     background: #8dd22b;
     background: linear-gradient(180deg, #8dd22b 0, #b1ec70);
 }
 
 .osjs-window[data-focused=true] .osjs-window-button[data-action=maximize]:active>div:before,
 .osjs-window[data-focused=true] .osjs-window-button[data-action=maximize]:hover>div:before {
     background: #b1ec70;
     background: linear-gradient(180deg, #b1ec70 0, #8dd22b);
 }
 
 .osjs-window[data-focused=true] .osjs-window-button[data-action=minimize]>div:before {
     background: #f9c435;
     background: linear-gradient(180deg, #f9c435 0, #fdd675);
 }
 
 .osjs-window[data-focused=true] .osjs-window-button[data-action=minimize]:active>div:before,
 .osjs-window[data-focused=true] .osjs-window-button[data-action=minimize]:hover>div:before {
     background: #fdd675;
     background: linear-gradient(180deg, #fdd675 0, #f9c435);
 }
 
 .osjs-window-title {
     text-align: center;
 }
 
 .osjs-window-header {
     padding: .5em;
 }
 
 .osjs-window-icon {
     margin-right: .5em;
 }
 
 
 
 /* Specific styling for the context menu inside the panel */
 .osjs-panel.osjs__contextmenu {
     position: fixed;
     bottom: 0;
     left: 0;    
     z-index: 1000;
     background-color: rgb(0, 0, 0);
     color: #fff;
     border: 1px solid #000000;
     padding: 10px;
     box-shadow: 0 2px 10px rgb(0 0 0 / 0%);
 }
   
   /* Styling for the context menu items */
   .osjs-panel.osjs__contextmenu ul {
     list-style: none;
     padding: 0;
     margin: 0;
   }
   
   .osjs-panel.osjs__contextmenu li {
     padding: 8px;
     cursor: pointer;
   }
   
   .osjs-panel.osjs__contextmenu li:hover {
     background-color: #555;  /* Highlight items on hover */
     color: #fff;
   }
   
 
 .osjs-panel {
     box-shadow: 0 0 1em rgba(0, 0, 0, .5);
     background: rgba(0, 0, 0, .9);
     color: #fff;
 }
 
